:root {
  --body: #1c1c1c;
  --list-item-background: rgb(52, 62, 76);
  --text-outline: -1px -1px 0 #000, 0 -1px 0 #000, 1px -1px 0 #000, 1px 0 0 #000,
    1px 1px 0 #000, 0 1px 0 #000, -1px 1px 0 #000, -1px 0 0 #000;
}
html,
body {
  background-color: var(--body);
  height: 100%;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}
#root {
  height: 100%;
}
.App {
  height: 100%;
  display: grid;
}
h1 {
  margin: 0;
}
.retainer-wrapper {
  grid-column: 1/3;
  justify-items: center;
  align-items: center;
  display: grid;
  background: rgb(34, 118, 195);
  background: linear-gradient(
    153deg,
    rgba(34, 118, 195, 1) 0%,
    rgba(45, 245, 253, 1) 100%
  );
}
.form-wrapper {
  /* background-color: lightslategrey; */
  padding: 20px;
  border-radius: 20px;
}
.retainer-form {
  display: grid;
}
.retainer-entry-title {
  text-shadow: var(--text-outline);
  color: white;
  /* opacity: 0.3; */
}
.retainer-entry {
  border: 0px solid black;
  border-bottom: 1px solid rgba(255, 255, 255, 0.61);
  /* background-color: hsl(215, 19%, 16%); */
  background-color: transparent;
  color: white;
  padding: 10px;
  margin-bottom: 10px;
  /* border-radius: 10px; */
  text-transform: capitalize;
}
.submit-button {
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
  background-color: #ffffff;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  display: inline-block;
  cursor: pointer;
  color: #666666;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
  width: 60%;
  margin-left: 20px;
}
.submit-button:hover {
  background: linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
  background-color: #f6f6f6;
  cursor: pointer;
}
.submit-button:active {
  position: relative;
  top: 1px;
}

.search-wrapper {
  width: 330px;
  height: max-content;
  overflow: overlay;
}
.search-wrapper:empty {
  background-color: red;
}
ul {
  list-style: none;
}
#header-search {
  background-color: var(--list-item-background);
  width: 100%;
  height: 40px;
  text-align: start;
  border: none;
  color: white;
  padding-left: 10px;
  border: 3px solid black;
}
::placeholder {
  color: white;
}
#header-search:hover {
  opacity: 0.9;
  cursor: pointer;
}
#header-search:focus {
  outline: none;
}
.suggestion-wrapper {
  border-top: 0px;
  border-left: 3px solid black;
  border-right: 3px solid black;
  border-bottom: 3px solid black;
}
.retainers {
  display: flex;
}
.retainer-edit {
  background: transparent;
  color: white;
  border: none;
  font-size: 2em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  font-family: inherit;
}
.retainer-edit:focus {
  outline: none !important;
}
.list-item {
  display: flex;
  color: white;
  background-color: var(--list-item-background);
  align-items: center;
  padding: 2px;
}
.list-item:not(:last-child) {
  border-bottom: solid 3px black;
}
.list-item * {
  margin: 0;
  margin-right: 10px;
  user-select: none;
}
.list-item:hover {
  opacity: 0.9;
  cursor: pointer;
}
.marketData-wrapper {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 3fr;
  text-align: center;
}
.marketData {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ilvl {
  color: gold;
}
.difference {
  color: rgb(206, 0, 0);
}
.controls {
  margin-left: auto;
}
i:hover {
  cursor: pointer;
}
.collapse {
  /* height: 0px; */
  visibility: collapse;
  /* transition: visibility 1s; */
}

.expand {
  transition: height 1s;
}
table {
  width: 100%;
  border-spacing: 0;
  margin: 0 !important;
  border-collapse: collapse;
  border-top: 0px;
  border-left: 1px solid black;
  border-bottom: 2px solid black;
}
thead {
  background-color: var(--list-item-background);
  text-transform: capitalize;
  color: white;
}
th {
  border-right: 1px solid #000;
  padding: 5px;
}
td {
  padding: 5px;
  border-right: 1px solid #000;
}

.item-listing {
  /* display: flex; */
  background-color: var(--list-item-background);
  /* width: 100%; */
}
.item-listing:nth-child(odd) {
  background-color: hsl(215, 23%, 28%);
}
.item-listing * {
  margin-left: 10px;
  color: white;
}
.item-listing p {
  text-shadow: var(--text-outline);
}
.position {
  color: lightgray;
}
.retainerName {
  text-align: left;
}
.price {
  color: gold;
}
.delete {
  font-size: 30px;
  -webkit-text-stroke: 1px black;
  font-family: Arial Black, Gadget, sans-serif;
  font-style: normal;
  margin-left: auto;
}
.highlight {
  background-color: #068488 !important;
}
